
import { defineComponent, computed, onMounted, PropType, watch } from "vue";
import mapboxgl from "mapbox-gl";
import "mapbox-gl/dist/mapbox-gl.css";
import MapboxLanguage  from '@mapbox/mapbox-gl-language'

export default defineComponent({
  props: {
    location: {
      type: Object as PropType<string[]>,
    },
  },
  setup(props) {
    let map: mapboxgl.Map;

    const computeCenter = (location: string[]): [number, number] => {
      if (location.length > 0) {
        const center: [number, number] = [0, 0];
        for (let i = 0; i < location.length; i = i + 2) {
          center[0] += parseFloat(location[i]);
          center[1] += parseFloat(location[i + 1]);
        }
        center[0] = center[0] / (location.length / 2);
        center[1] = center[1] / (location.length / 2);
        return center;
      } else {
        return [121.18, 31.723];
      }
    };

    const coordinates = computed(() => {
      if (props.location!.length > 0) {
        const result: number[][] = [];
        for (let i = 0; i + 1 < props.location!.length; i = i + 2) {
          result.push([
            parseFloat(props.location![i]),
            parseFloat(props.location![i + 1]),
          ]);
        }
        return result;
      } else {
        return [];
      }
    });

    const setData = () => {
      if (coordinates.value.length > 0) {
        map.setCenter(computeCenter(props.location!));
        map.removeLayer("someS id");
        map.removeSource("someS id");
        map.addSource("someS id", {
          type: "geojson",
          data: {
            type: "FeatureCollection",
            features: [
              {
                type: "Feature",
                properties: {},
                geometry: {
                  type: "Polygon",
                  coordinates: [coordinates.value],
                },
              },
            ],
          },
        });

        map.addLayer({
          id: "someS id",
          source: "someS id",
          type: "fill",
          layout: {},
          paint: {
            "fill-color": "#6495ED",
            "fill-opacity": 0.5,
          },
        });
      }
    };

    const initMap = () => {
      map = new mapboxgl.Map({
        container: "map2",
        // 这是osm的style
        style: "mapbox://styles/mapbox/streets-v12",

        // 下方是天地图的style
        // style: {
        //   version: 8,
        //   sources: {
        //     tdtVec: {
        //       type: "raster",
        //       tiles: [
        //         "http://t0.tianditu.gov.cn/vec_w/wmts?tk=35a94ab5985969d0b93229c30db6abd6&SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=vec&STYLE=default&TILEMATRIXSET=w&TILEMATRIX={z}&TILEROW={y}&TILECOL={x}&FORMAT=tiles",
        //       ],
        //       tileSize: 256,
        //     },
        //     txt: {
        //       type: "raster",
        //       tiles: [
        //         "http://t0.tianditu.gov.cn/cva_w/wmts?tk=35a94ab5985969d0b93229c30db6abd6&SERVICE=WMTS&REQUEST=GetTile&VERSION=1.0.0&LAYER=cva&STYLE=default&TILEMATRIXSET=w&TILEMATRIX={z}&TILEROW={y}&TILECOL={x}&FORMAT=tiles",
        //       ],
        //       tileSize: 256,
        //     },
        //   },
        //   layers: [
        //     {
        //       id: "tdtVec",
        //       type: "raster",
        //       source: "tdtVec",
        //     },
        //     {
        //       id: "txt",
        //       type: "raster",
        //       source: "txt",
        //     },
        //   ],
        // },
        center: computeCenter(props.location!),
        zoom: 7,
        accessToken:
          "pk.eyJ1Ijoiam9obm55dCIsImEiOiJja2xxNXplNjYwNnhzMm5uYTJtdHVlbTByIn0.f1GfZbFLWjiEayI6hb_Qvg",
      });
      
      var language = new MapboxLanguage({ defaultLanguage: "zh-Hans" });
      map.addControl(language);

      map.on("load", () => {
        map.addSource("someS id", {
          type: "geojson",
          data: {
            type: "FeatureCollection",
            features: [
              {
                type: "Feature",
                properties: {},
                geometry: {
                  type: "Polygon",
                  coordinates: [coordinates.value],
                },
              },
            ],
          },
        });

        map.addLayer({
          id: "someS id",
          source: "someS id",
          type: "fill",
          layout: {},
          paint: {
            "fill-color": "#6495ED",
            "fill-opacity": 0.5,
          },
        });
      });
    };

    watch(
      () => [...props.location!],
      (newVal) => {
        setData();
      }
    );

    onMounted(() => {
      initMap();
    });

    return {
      setData,
      coordinates,
    };
  },
});
