
import { computed, defineComponent } from "vue";
export default defineComponent({
  props: {
    data: {
      type: Object,
    },
  },
  setup(props) {
    const key = computed(() => {
      return (props.data as any).key;
    });
    const value = computed(() => {
      return (props.data as any).value;
    });

    return {
      key,
      value,
    };
  },
});
